// height: 50px;
// line-height: 50px;
// font-size: 18px;
// border-radius: 12px;
// display: inline-block;
// text-transform: none;
// }

input.login-input {
    background: $bg-content;
}

.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 78px;
    // background: #11101D;
    // background: $bg-color;
    background: rgb(0,0,0);
    background: linear-gradient(145deg, rgba(0,0,0,1) 0%, rgba(2,0,36,1) 20%, rgba(56,68,83,1) 100%); 
    padding: 6px 14px;
    z-index: 99;
    transition: all 0.5s ease;
}

.sidebar.open {
    width: 250px;
}

.sidebar .logo-details {
    height: 60px;
    display: flex;
    align-items: center;
    position: relative;
}

.sidebar .logo-details .icon {
    opacity: 1;
    transition: all 0.5s ease;
}

.sidebar .logo-details .logo_name {
    color: #fff;
    color: $font-color;
    font-size: 20px;
    font-weight: 600;
    opacity: 0;
    transition: all 0.5s ease;
    display: none;
}

.sidebar.open .logo-details .icon,
.sidebar.open .logo-details .logo_name {
    opacity: 1;
}

.sidebar .logo-details #btn {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 22px;
    transition: all 0.4s ease;
    font-size: 23px;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s ease;
}

.sidebar.open .logo-details #btn {
    text-align: right;
}

.sidebar i {
    color: #fff;
    color: $font-color;
    height: 60px;
    min-width: 50px;
    font-size: 28px;
    text-align: center;
    line-height: 60px;
}

.sidebar .nav-list {
    margin-top: 20px;
    height: 100%;
}

.sidebar li {
    position: relative;
    margin: 8px 0;
    list-style: none;
}

.sidebar li .tooltip {
    position: absolute;
    top: -20px;
    left: calc(100% + 15px);
    z-index: 3;
    background: #fff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 400;
    opacity: 0;
    white-space: nowrap;
    pointer-events: none;
    transition: 0s;
}

.sidebar li:hover .tooltip {
    opacity: 1;
    pointer-events: auto;
    transition: all 0.4s ease;
    top: 50%;
    transform: translateY(-50%);
}

.sidebar.open li .tooltip {
    display: none;
}

.sidebar input {
    font-size: 15px;
    color: #FFF;
    font-weight: 400;
    outline: none;
    height: 50px;
    width: 100%;
    width: 50px;
    border: none;
    border-radius: 12px;
    transition: all 0.5s ease;
    background: #1d1b31;
}

.sidebar.open input {
    padding: 0 20px 0 50px;
    width: 100%;
}

.sidebar .bx-search {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 22px;
    background: #1d1b31;
    color: #FFF;
}

.sidebar.open .bx-search:hover {
    background: #1d1b31;
    color: #FFF;
}

.sidebar .bx-search:hover {
    background: #FFF;
    color: #11101d;
}

.sidebar li a {
    display: flex;
    height: 100%;
    width: 100%;
    border-radius: 12px;
    align-items: center;
    text-decoration: none;
    transition: all 0.4s ease;
    // background: #11101D;
    // background: $bg-color;    
    background: rgb(255, 255, 255, 0.05);
}

.sidebar li a:hover {
    background: #FFF;
    background: $bg-color;

    span {
        color: #11101D;
        color: $font-color;
    }
}

.sidebar li a .links_name {
    color: #fff;
    color: $font-color;
    font-size: 15px;
    font-weight: 400;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: 0.4s;
}

.sidebar.open li a .links_name {
    opacity: 1;
    pointer-events: auto;
}

.sidebar li a .links_name_short {
    //opacity: 1;
    display: inline-block;
    color: $font-color;
    font-style: normal;
    font-size: 80%;
    padding: 0px 0px 0px 5px;
    vertical-align: top;
}

.sidebar.open li a .links_name_short {
    //opacity: 0;
    display: none;
}

.sidebar li a:hover .links_name,
.sidebar li a:hover i {
    transition: all 0.5s ease;
    //color: #11101D;
}

.sidebar li i {
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    border-radius: 12px;
}

.sidebar li.profile {
    position: fixed;
    height: 60px;
    width: 78px;
    left: 0;
    bottom: -8px;
    padding: 10px 14px;
    background: #1d1b31;
    transition: all 0.5s ease;
    overflow: hidden;
}

.sidebar.open li.profile {
    width: 250px;
}

.sidebar li .profile-details {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}

.sidebar li img {
    height: 45px;
    width: 45px;
    object-fit: cover;
    border-radius: 6px;
    margin-right: 10px;
}

.sidebar li.profile .name,
.sidebar li.profile .job {
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    color: $font-color;
    white-space: nowrap;
}

.sidebar li.profile .job {
    font-size: 12px;
}

.sidebar .profile #log_out {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background: #1d1b31;
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-radius: 0px;
    transition: all 0.5s ease;
}

.sidebar.open .profile #log_out {
    width: 50px;
    background: none;
}

.content-box {
    position: relative;
    background: $bg-content;
    min-height: 100vh;
    top: 0;
    left: 78px;
    width: calc(100% - 78px);
    transition: all 0.5s ease;
    z-index: 2;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    /* display: "flex";
    flex-direction: "column";
    justify-content: "flex-start";
    min-height: "100vh";
    max-width: "100vw";
    fflex-grow: 1;  */
}

.sidebar.open~.content-box {
    left: 250px;
    width: calc(100% - 250px);
}

.content-box .text {
    display: inline-block;
    color: #11101d;
    font-size: 25px;
    font-weight: 500;
    margin: 18px
}

.content-box header a {
    text-decoration: none;
    color: $bg-color;
    padding: 0px 15px;
    border-right: 1px solid $font-color;

    &:last-child {
        border-right: none
    }
}

a.nav-link-mobile {
    text-decoration: none;
    color: $bg-color;

    p {
        padding: 5px 10px;
    }
}

@media (max-width: 420px) {
    .sidebar li .tooltip {
        display: none;
    }
}


// @media (max-width: 768px) {
//     .sidebar li i::after {       
//         content: " 0";
//         font-style: normal;
//         font-size: 80%;
//     }
// }

@media (min-width: 768px) {
    .sidebar {
        width: 250px;
    }

    .sidebar~.content-box {
        left: 250px;
        width: calc(100% - 250px);
    }

    .sidebar .logo-details .icon,
    .sidebar .logo-details .logo_name {
        opacity: 1;
    }

    .sidebar .logo-details .logo_name {
        display: block;
    }

    .sidebar .logo-details #btn {
        text-align: right;
    }

    .sidebar li a .links_name {
        opacity: 1;
        pointer-events: auto;
    }

    .sidebar li a .links_name_short {
        //opacity: 0;        
        display: none;
    }

    .sidebar li.profile {
        width: 250px;
    }

    .sidebar li .tooltip {
        display: none;
    }
}

.sidebar li .links_name_short {
    &.orange-zone {
        color: #FF6600;
        //border-left: 1px solid #FF6600;
    }

    &.yellow-zone {
        color: #fff701;
        //border-left: 1px solid #fff701;
    }

    &.green-zone {
        color: #0bd500;
        //border-left: 1px solid #0bd500;
    }

    &.blue-zone {
        color: #001eff;
        //border-left: 1px solid #001eff;
    }
}